body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f9f9f9;
    color: #333;
  }
  
  .app-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .header {
    background-color: #6200ea;
    color: white;
    text-align: center;
    padding: 1rem;
  }
  
  main {
    flex: 1;
    padding: 2rem;
    text-align: center;
  }
  
  .ticket-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-top: 2rem;
  }
  
  .ticket-form label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
    width: 100%;
    max-width: 400px;
  }
  
  .ticket-form input,
  .ticket-form textarea {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .ticket-form button {
    background-color: #6200ea;
    color: white;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .ticket-form button:hover {
    background-color: #3700b3;
  }
  
  .footer {
    background-color: #6200ea;
    color: white;
    text-align: center;
    padding: 1rem;
  }
  